@import './assets/fonts/stylesheet.css';

:root {
  /* TODO: change based on figma file */
  background-color: black;
  color: white;
  --primary: #2A5C65;
  --secondary-bg: #0F0F0F;
  --accent: #1f1f1f;
  --offwhite: #b3b3b3;
  --tertiary-bg: #1a1a1a;
  --darkgray: #ccc;
  --mgvwidth: 600px;
  letter-spacing: 0.5px;
  font-size: 19.5px;
  font-family: 'ProximaNovaRegular', Helvetica, sans-serif !important;
}

.bg2 {
  background-color: var(--secondary-bg);
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: white;
  /* color: var(--accent); */
}

div.main {
  margin: 77px;
  margin-inline: 0px;
}

div.home>* {
  margin: 80px;
  margin-inline: auto;
}

a.button {
  padding-inline: 15px;
  color: white;
  font-size: 1.33rem;
  border-radius: 5px;
}

p.shady-70 {
  opacity: 70%;
}

p {
  text-align: left;
}

.accent {
  color: var(--accent);
}

.offwhite {
  color: var(--offwhite);
}

.title {
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
  padding: 0;
  margin: 0;
}

.subtitle {
  font-size: 3rem;
  padding: 0;
  margin: 0;
}

.heading {
  font-size: 1.33rem;
  font-weight: bold;
}

.heading-colored {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent);
}

a.footerLink {
  font-size: 1rem;
  color: white;
  padding: 5px;
}

.col {
  display: flex;
  flex-direction: column;
}

.centercol {
  align-items: center;
}

.centerrow {
  justify-content: center;
}

.row {
  display: flex;
  flex-direction: row;
}

.rrow {
  display: flex;
  flex-direction: row-reverse;
}

/* input{
  height: 30px;
  border: 3px solid black;
  padding: 5px;
  border-radius: 5px;
} */
img.profile {
  border-radius: 28px;
  width: 56px;
  height: 56px;
  margin-right: 16px;
}
div.blogtile{
  height:250px;
  overflow: hidden;
}
div.episodetile{
  height:230px;
  overflow: hidden;
}
.student-img {
  width: 250px;
  height: 250px;
}

.person-img {
  width: 200px;
  height: 200px;
}

.preview-img {
  width: 30px;
  height: 30px;
}

hr.thick {
  border: 0;
  clear: both;
  display: block;
  width: 100%;
  background-color: var(--secondary-bg);
  height: 5px;
}

hr.thin {
  border: 0;
  clear: both;
  display: block;
  width: 100%;
  background-color: var(--secondary-bg);
  height: 1px;
}

p.stripped {
  margin: 3px;
  padding: 0;
}

.thin-bordered {
  border: 0.5px solid var(--accent);
}

.titlebar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

a.button2 {
  background-color: var(--primary);
  padding-inline: 16px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
}
.centered{
  text-align: center;
}
.gradient-shade{
  background-image: linear-gradient(to top, rgba(0,0,0,1),rgba(0,0,0,0.7),rgba(0,0,0,0));
}
.left{
  text-align: left;
}
a.button-pill {
  font-size: 0.7rem;
  border-radius: 30px;
  background-color: var(--tertiary-bg);
  padding: 8px;
  padding-inline: 16px;
  text-transform: uppercase;
}
.clocked::before{
  content: url('assets/clock.svg');
  position: relative;
  display: inline-block;
  vertical-align: middle;
  right: 0px;
  margin: 0px;
  padding-right: 8px;
  align-self: center;
}
a.caret-right::after {
  content: url('assets/caretRight.svg');
  position: relative;
  display: inline-block;
  vertical-align: middle;
  right: 0px;
  margin: 0px;
  padding-left: 4px;
  align-self: center;

}

.fs-small {
  font-size: 0.8rem;
}

.fs-smaller {
  font-size: 0.6rem;
}

.fs-large {
  font-size: 1.33rem;
}

.fs-x-large {
  font-size: 1.66rem;
}

.fs-medium {
  font-size: 1rem;
}

.fs-larger {
  font-size: 1.5rem;
}

.fs-xx-large {
  font-size: 2rem;
}

.pointable {
  cursor: pointer;
}

.desktop {
  display: none;
}

@media(min-width:800px) {
  .desktop {
    display: block;
  }

  .mobile {
    display: none;
  }
}

.shadow {
  box-shadow: 3px 3px 5px 5px grey;
}

.titlebar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

img.logo {
  width: 100px;
  height: 100px;
  padding: 10px;
}

div.grid {
  display: grid;
}

/* dimensions: 3 x n */
div.d3xn {
  grid-template-columns: 33% 33% 33%;
  align-items: center;
}

.articlesgrid {
  display: grid;
  grid-template-columns: 100%;
  align-items: center;
}

@media (min-width:750px) {
  .articlesgrid {
    grid-template-columns: 50% 50%;
  }
}

@media (min-width:1200px) {
  .articlesgrid {
    grid-template-columns: 33% 33% 33%;
  }
}

.mentionsgrid {
  display: grid;
  grid-template-columns: 100%;
  align-items: center;
}

@media (min-width:1200px) {
  .mentionsgrid {
    grid-template-columns: 50% 50%;
  }
}

.field.title {
  margin-top: 32px;
  margin-bottom: 0px;
  font-size: small;
  text-align: start;
}

.field.input {
  resize: none;
  position: relative;
  left: 0;
  right: 0;
  font-family: 'ProximaNovaRegular', Helvetica, sans-serif;
  margin-top: 12px;
  margin-bottom: 0;
  border: 1px solid #1f1f1f;
  border-radius: 5px;
  color: white;
  background-color: var(--secondary-bg);
  padding: 12px;
  padding-inline: 16px;
}

.field.input:focus-visible {
  outline: none;
}